@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.burger {
  position: absolute;
  right: 5vw;
  top: 2.5vh;
  z-index: 51;
  padding: 2px;
  border: none;
  cursor: pointer; }
  .burger .burger-img {
    height: 5vh;
    background-color: rgba(0, 0, 0, 0);
    transition: .3s; }
  .burger .close {
    right: 5vw;
    top: 2.5vh;
    position: fixed;
    height: 4vh;
    -webkit-filter: invert(1);
    filter: invert(1); }

.App {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: .7s; }
  .App .background {
    position: fixed;
    background-color: #000;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: -50; }
    @media (min-width: 400px) {
      .App .background {
        width: 100vw; } }
  .App header {
    position: relative;
    height: 75vh; }
    @media (min-width: 576px) {
      .App header {
        height: 100vh; } }
    .App header nav .kangaroo-container {
      position: relative;
      width: 100%;
      height: 11vh;
      background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1)); }
      .App header nav .kangaroo-container .kangaroo {
        position: absolute;
        left: 5vw;
        top: 50%;
        transform: translate(0, -50%);
        height: 7vh; }
    .App header .main-logo {
      position: absolute;
      left: 50%;
      top: 46%;
      transform: translate(-50%, -50%);
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .App header .main-logo h1 {
        font-size: 25px; }
      .App header .main-logo h2 {
        font-size: 46px; }
  .App main .image-box {
    display: flex;
    width: 100%;
    border-radius: 20px;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-around;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px 0; }
    @media (min-width: 576px) {
      .App main .image-box {
        flex-direction: column;
        flex-wrap: wrap; } }
    .App main .image-box div {
      padding: 10px 20px;
      flex-basis: 100%; }
      @media (min-width: 576px) {
        .App main .image-box div {
          flex-basis: 20%; } }
      @media (min-width: 768px) {
        .App main .image-box div {
          flex-basis: 33%; } }
      .App main .image-box div img {
        width: 100%; }
  .App section.about-me {
    position: relative;
    min-height: 65vh; }
    .App section.about-me .about-me-container {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%); }
      .App section.about-me .about-me-container .about-me-wrap {
        margin-top: 220px;
        padding-top: 20px;
        background-color: rgba(255, 255, 255, 0.5);
        border-radius: 20px;
        width: 100vw;
        font-family: 'Permanent Marker', cursive;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 3px; }
        .App section.about-me .about-me-container .about-me-wrap .top {
          margin-bottom: 10px; }
          .App section.about-me .about-me-container .about-me-wrap .top h2 {
            font-size: 23px;
            margin-top: 20px;
            margin-bottom: -5px; }
          .App section.about-me .about-me-container .about-me-wrap .top h1 {
            font-size: 49px; }
        .App section.about-me .about-me-container .about-me-wrap .body {
          padding: 20px;
          font-family: 'Permanent Marker', cursive;
          text-align: center;
          text-transform: uppercase;
          letter-spacing: 3px; }
      .App section.about-me .about-me-container .about-me-box {
        display: flex;
        position: relative;
        width: 65%;
        border-radius: 20px;
        flex-wrap: wrap;
        justify-content: center;
        align-content: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding: 10px 0;
        margin-left: 30px;
        margin-top: 40px; }
        .App section.about-me .about-me-container .about-me-box div {
          flex-basis: 100%;
          transform: translate(38%, 0);
          padding: 10px 20px; }
          .App section.about-me .about-me-container .about-me-box div img {
            width: 100%;
            border: 4px solid rgba(0, 0, 0, 0.8);
            border-radius: 20px;
            margin: 0 auto; }
  .App footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: #000000; }
    .App footer p {
      margin-top: 12px;
      line-height: 12px;
      color: #6969696e;
      font-size: 9px;
      letter-spacing: 3px;
      text-transform: uppercase;
      font-family: Arial, "Helvetica Neue", Helvetica, sans-serif; }

