*{
  box-sizing: border-box;
}
.menu-hamburger {
  position: fixed;
  z-index: 50;
  left: 100%;
  top: 0;
  padding-top: 12vh;
  list-style: none;
  text-align: center;
  width: 85%;
  height: 100%;
  /* background-color: rgba(0, 0, 0, .87); */
  color: #ffffff;
  background-color: #000000;
  transition: .5s;
}

.kangaroo-menu {
  padding-bottom: 50px;
  height: 150px;
  -webkit-filter: invert(1);
  filter: invert(1);
}

.li {
  list-style: none;
  margin-top: 7vh;
  margin-bottom: 13vh;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-family: 'Permanent Marker', cursive;
  font-size: 1.5rem;
  text-decoration: none !important;
  color: #ffffff !important;
  cursor: pointer;
}

.active {
  left: 15%;
  top: 0;
}
.activePortfolio {
  left: -100%;
  top: 0;
}